/* ---------- swiper css -------------- */


#app { height: 100% }
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

.swiper {
  margin-top: 2rem;
  width: 100%;
  height: 160px;
  padding: 5rem;
  /* background-color: rgb(0, 0, 0); */
  border-radius: 15px;

}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img{
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.13);
}



.MuiButtonBase-root {
  color:#fff !important;
}
/* ----------------- carousel_container  ----------------- */

.carousel_container{
  
  min-width: 210px;
  min-height: 150px;
  /* background: rgb(99, 98, 98)  */
  display: grid;
  grid-template-columns: 70px 1fr;
  grid-template-rows: 70px 1fr .5fr;
  color: white;
  gap: .5rem;
  padding: .5rem;
}

.swiper-slide{
  background-color: rgb(26, 26, 26) !important;
}

.carousel_container div{
 font-size: 13px;
 color: #FFFFFF99;
 text-align: left;
 /* margin-left: .5rem; */
}

.carousel_container div:nth-child(2) div:first-child{
 font-size: 15px;
 margin-left: .5rem;
 overflow: hidden;
 border-radius: 5px !important;
}
.carousel_container div:nth-child(2) div:nth-child(2){
  margin-left: .5rem;
  margin-top: .5rem;
  font-size: 13px;
  color: #FFFFFF99;
}
.carousel_container div:nth-child(2) div:last-child{
  margin-left: .5rem;
  font-size: 13px;
  margin-top: .5rem;
  
}

.carousel_container div:nth-child(3){
  font-size: 13px;
  color: #FFFFFF99;
}
.carousel_container div:nth-child(4){
  margin-left: .5rem;
 
}


.carousel_container div:last-child{
  grid-column: 1/-1;
  font-size: 11px;
}

select{
  padding: .5rem;
  width: 90%;
  margin: .7rem .5rem;
  background-color: rgb(26, 26, 26);
  color: white;
  border: none;
  outline:none ;
  font-size: 14px;
}






/* ####### media query ###### start  */

@media screen and (max-width: 760px) {
  body {
    background-color: lightgreen;
  }
  .container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    grid-template-columns: minmax(200px, 300px) minmax(100px , 1400px);
    background-color: #292929;
}
  .main_heading {
    margin-left: 1.5rem;
}
}





@media only screen and (max-width: 1200px) {


.Filter_options{
  width: auto !important;
}
}

/* ####### media query ###### end */